#my-page {
    position: relative;
    width: 60%;
    @media (max-width: 769px) {
        width: 90%;
    }
    @media (max-width: 511px) {
        width: 100%;
    }

    .title-for-page {
        margin-top: 6rem;
        @media (max-width: 769px) {
            margin-top: 3rem;
        }

        h2 {
            margin-left: 1rem;
        }

        // .border-bottom-gach {
